import React, { Component } from "react";
import Node from "./node";
import { dijkstra } from "../algorithms/dijkstra";

import "./pathfinder.css";

const START_NODE_ROW = 25;
const START_NODE_COLUMN = 10;
const FINISH_NODE_ROW = 25;
const FINISH_NODE_COLUMN = 40;

class Pathfinder extends Component {
	constructor() {
		super();
		this.state = {
			grid: [],
			isMouseDown: false,
		};
	}

	componentDidMount() {
		const grid = createGrid();
		this.setState({ grid });
	}

	handleMouseDown(row, column) {
		const newGrid = getNewGridWithWallToggled(this.state.grid, row, column);
		this.setState({ grid: newGrid, isMouseDown: true });
	}

	handleMouseUp() {
		this.setState({ isMouseDown: false });
	}

	handleMouseEnter(row, column) {
		if (!this.state.isMouseDown) return;
		const newGrid = getNewGridWithWallToggled(this.state.grid, row, column);
		this.setState({ grid: newGrid });
	}

	render() {
		const { grid, isMouseDown } = this.state;
		return (
			<>
				<button onClick={() => this.visualizeDijkstra()}>Visualize Dijkstra's Algorithm</button>
				<div className="grid">
					{grid.map((row, rowIndex) => {
						return (
							<div key={rowIndex}>
								{row.map((node, nodeIndex) => {
									const { row, column, isStart, isFinish } = node;
									return (
										<Node
											key={nodeIndex}
											row={row}
											column={column}
											isStart={isStart}
											isFinish={isFinish}
											isMouseDown={isMouseDown}
											onMouseDown={(row, column) => this.handleMouseDown(row, column)}
											onMouseUp={() => this.handleMouseUp()}
											onMouseEnter={(row, column) => this.handleMouseEnter(row, column)}
										></Node>
									);
								})}
							</div>
						);
					})}
				</div>
			</>
		);
	}
}

const createGrid = () => {
	const grid = [];
	for (let row = 0; row < 50; ++row) {
		const currentRow = [];
		for (let column = 0; column < 50; ++column) {
			currentRow.push(createNode(row, column));
		}
		grid.push(currentRow);
	}
	return grid;
};

const createNode = (row, column) => {
	return {
		row,
		column,
		isStart: row === START_NODE_ROW && column === START_NODE_COLUMN,
		isFinish: row === FINISH_NODE_ROW && column === FINISH_NODE_COLUMN,
		isWall: false,
	};
};

const getNewGridWithWallToggled = (grid, row, column) => {
	const newGrid = grid.slice();
	const node = newGrid[row][column];
	const newNode = {
		...node,
		isWall: !node.isWall,
	};
	newGrid[row][column] = newNode;
	return newGrid;
};

export default Pathfinder;
